<h1 id="exam-2022-03-26" tabindex="-1"><a class="header-anchor" href="#exam-2022-03-26" aria-hidden="true">#</a> Exam 2022-03-26</h1>
<p>Here you find sample answers and marking guidelines to the questions on the exam 2022-03-26.</p>
<hr>
<p>Start by reading through all questions.</p>
<p>Max score is 30 points.</p>
<ul>
<li>For grade 3, 40% of max score (12 points) is required</li>
<li>For grade 4, 60% of max score (18 points) is required</li>
<li>For grade 5, 80% of max score (24 points) is required</li>
</ul>
<p>You are not allowed to use anything but:</p>
<ul>
<li>The computer you sit at to only answer the questions on this exam</li>
<li>A dictionary to translate to/from English from/to your native language</li>
<li>Pen and paper to sketch with (should not be submitted)</li>
</ul>
<p>Write your answers in either English or Swedish. If you write your answers in Swedish, make sure to not introduce any translation confusement. <strong>Write proper sentences (spelling, upper/lower case characters, punctuation, etc.).</strong> Each answer that contains multiple obvious errors, such as:</p>
<ul>
<li>A sentence starting with a lower case character</li>
<li>A sentence not ending with a period, question mark, or any other suitable character</li>
<li>A sentence that is incomplete</li>
<li>A sentence that is incomprehensible</li>
</ul>
<p>Will automatically lose 0.25 points. The ability to express yourself in text clearly is very important for an engineer, and therefor part of the examination.</p>
<p>Answers that are more or less copies of sample answers given to you or copies of text found somewhere else will be rewarded 0 points. Use your own words to answer the questions.</p>
<p>Good luck!</p>
<h2 id="client-side" tabindex="-1"><a class="header-anchor" href="#client-side" aria-hidden="true">#</a> Client-side</h2>
<h3 id="question-1-1p" tabindex="-1"><a class="header-anchor" href="#question-1-1p" aria-hidden="true">#</a> Question 1 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>The code below will result in a runtime error when it's executed. Which is the runtime error, and what changes do you need to do to fix the error?</p>
<div class="language-html ext-html line-numbers-mode"><pre v-pre class="language-html"><code><span class="token doctype"><span class="token punctuation">&lt;!</span><span class="token doctype-tag">DOCTYPE</span> <span class="token name">html</span><span class="token punctuation">></span></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>html</span> <span class="token attr-name">lang</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>en<span class="token punctuation">"</span></span><span class="token punctuation">></span></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>head</span><span class="token punctuation">></span></span>
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>meta</span> <span class="token attr-name">charset</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>UTF-8<span class="token punctuation">"</span></span><span class="token punctuation">></span></span>
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>title</span><span class="token punctuation">></span></span>Clock<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>title</span><span class="token punctuation">></span></span>
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>script</span><span class="token punctuation">></span></span><span class="token script"><span class="token language-javascript">
        
        <span class="token keyword">function</span> <span class="token function">startClock</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
            
            <span class="token keyword">const</span> clockSpan <span class="token operator">=</span> document<span class="token punctuation">.</span><span class="token function">getElementById</span><span class="token punctuation">(</span><span class="token string">'clock'</span><span class="token punctuation">)</span>
            
            <span class="token keyword">function</span> <span class="token function">updateClock</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
                
                <span class="token keyword">const</span> now <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">Date</span><span class="token punctuation">(</span><span class="token punctuation">)</span>
                
                clockSpan<span class="token punctuation">.</span>innerText <span class="token operator">=</span> now<span class="token punctuation">.</span><span class="token function">toString</span><span class="token punctuation">(</span><span class="token punctuation">)</span>
                
            <span class="token punctuation">}</span>
            
            <span class="token comment">// Call updateClock each second in the future.</span>
            <span class="token function">setInterval</span><span class="token punctuation">(</span>updateClock<span class="token punctuation">,</span> <span class="token number">1000</span><span class="token punctuation">)</span>
            
            <span class="token comment">// setInterval doesn't call updateClock immediately,</span>
            <span class="token comment">// so we call it immediately ourselves. </span>
            <span class="token function">updateClock</span><span class="token punctuation">(</span><span class="token punctuation">)</span>
            
        <span class="token punctuation">}</span>
        
        <span class="token function">startClock</span><span class="token punctuation">(</span><span class="token punctuation">)</span>
        
    </span></span><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>script</span><span class="token punctuation">></span></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>head</span><span class="token punctuation">></span></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>body</span><span class="token punctuation">></span></span>
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>span</span> <span class="token attr-name">id</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>clock<span class="token punctuation">"</span></span><span class="token punctuation">></span></span><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>span</span><span class="token punctuation">></span></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>body</span><span class="token punctuation">></span></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>html</span><span class="token punctuation">></span></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br><span class="line-number">21</span><br><span class="line-number">22</span><br><span class="line-number">23</span><br><span class="line-number">24</span><br><span class="line-number">25</span><br><span class="line-number">26</span><br><span class="line-number">27</span><br><span class="line-number">28</span><br><span class="line-number">29</span><br><span class="line-number">30</span><br><span class="line-number">31</span><br><span class="line-number">32</span><br><span class="line-number">33</span><br><span class="line-number">34</span><br><span class="line-number">35</span><br><span class="line-number">36</span><br></div></div></template>
<template v-slot:sample-answer>
<p>The runtime error you get is that <code>clockSpan</code> will be <code>null</code>, and you can't access the property <code>innerText</code> of it. You get that because <code>document.getElementById('clock')</code> will be executed before the web browser has read all HTML code, so it won't find the element we're looking for, and instead return back <code>null</code>.</p>
<p>To fix the problem, you need to make sure <code>startClock()</code> is called after the web browser has read all HTML code. To make that happen, replace the following line of code:</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token function">startClock</span><span class="token punctuation">(</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>With:</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code>document<span class="token punctuation">.</span><span class="token function">addEventListener</span><span class="token punctuation">(</span><span class="token string">'DOMContentLoaded'</span><span class="token punctuation">,</span> startClock<span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div></template>
<template v-slot:marking-guidelines>
<ul>
<li>0.5 points for identifying the runtime error
<ul>
<li>Saying that it's an error that <code>startClock()</code> is called before the web browser has read all HTML code is correct, but that is not the <em>runtime error</em> that occurs</li>
<li>Although the runtime error occurs when trying to read from <code>clockSpan</code>, I have rewarded answers that indicate that <code>document.getElementById('clock')</code> is the problem with 0.25 points (the runtime error would occur here if <code>document.getElementById()</code> would throw an exception instead of returning <code>null</code>)</li>
</ul>
</li>
<li>0.5 points for functional solution</li>
</ul>
</template>
</ExamQuestion>
<h3 id="question-2-1p" tabindex="-1"><a class="header-anchor" href="#question-2-1p" aria-hidden="true">#</a> Question 2 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>On a webpage with three different <code>&lt;form&gt;</code> elements, Bob needs to add one and the same submit listener to all of them. To do that, he creates the helper function <code>addSubmitListenerToForm(formId)</code>, which is implemented like this:</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">function</span> <span class="token function">onSubmit</span><span class="token punctuation">(</span><span class="token parameter">event</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    <span class="token comment">// Some code that handles the form submission</span>
    <span class="token comment">// (which code is not important for the question).</span>
<span class="token punctuation">}</span>

<span class="token keyword">function</span> <span class="token function">addSubmitListenerToForm</span><span class="token punctuation">(</span><span class="token parameter">formId</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    
    <span class="token keyword">const</span> form <span class="token operator">=</span> document<span class="token punctuation">.</span><span class="token function">getElementById</span><span class="token punctuation">(</span>formId<span class="token punctuation">)</span>
    
    form<span class="token punctuation">.</span><span class="token function">addEventListener</span><span class="token punctuation">(</span><span class="token string">'submit'</span><span class="token punctuation">,</span> <span class="token function">onSubmit</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">)</span>
    
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br></div></div><p>Then, somewhere in the rest of his code, he calls <code>addSubmitListenerToForm()</code> three times, and pass it the id of respective <code>&lt;form&gt;</code> each time.</p>
<p>The code above contains an obvious error. Which is that error, and what changes do you need to do to fix it?</p>
</template>
<template v-slot:sample-answer>
<p>The error is on this line of code:</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code>form<span class="token punctuation">.</span><span class="token function">addEventListener</span><span class="token punctuation">(</span><span class="token string">'submit'</span><span class="token punctuation">,</span> <span class="token function">onSubmit</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>Here, the <code>onSubmit()</code> function is currently called, and its return value is passed passed to <code>addEventListener()</code>. That's not how it should work, because the <code>onSubmit()</code> function itself should be passed to <code>addEventListener()</code>. So, the correct code is:</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code>form<span class="token punctuation">.</span><span class="token function">addEventListener</span><span class="token punctuation">(</span><span class="token string">'submit'</span><span class="token punctuation">,</span> onSubmit<span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div></template>
<template v-slot:marking-guidelines>
<ul>
<li>0.5 points for explaining the error</li>
<li>0.5 points for suggesting a correct fix</li>
</ul>
</template>
</ExamQuestion>
<h3 id="question-3-1p" tabindex="-1"><a class="header-anchor" href="#question-3-1p" aria-hidden="true">#</a> Question 3 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>In a Single-Page Application (SPA), the following HTML code is created and inserted into the DOM using client-side JavaScript (<code>document.createElement()</code>, etc.) and shown to the user when the user navigates to the login page in the SPA:</p>
<div class="language-html ext-html line-numbers-mode"><pre v-pre class="language-html"><code><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>h1</span><span class="token punctuation">></span></span>Login<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>h1</span><span class="token punctuation">></span></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>form</span> <span class="token attr-name">id</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>login<span class="token punctuation">"</span></span><span class="token punctuation">></span></span>
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>div</span><span class="token punctuation">></span></span>
        Username: <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>input</span> <span class="token attr-name">type</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>text<span class="token punctuation">"</span></span> <span class="token attr-name">id</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>username<span class="token punctuation">"</span></span><span class="token punctuation">></span></span>
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>div</span><span class="token punctuation">></span></span>
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>div</span><span class="token punctuation">></span></span>
        Password: <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>input</span> <span class="token attr-name">type</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>password<span class="token punctuation">"</span></span> <span class="token attr-name">id</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>password<span class="token punctuation">"</span></span><span class="token punctuation">></span></span>
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>div</span><span class="token punctuation">></span></span>
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>input</span> <span class="token attr-name">type</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>submit<span class="token punctuation">"</span></span> <span class="token attr-name">value</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>Login<span class="token punctuation">"</span></span><span class="token punctuation">></span></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>form</span><span class="token punctuation">></span></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br></div></div><p>Immediately after that, the following client-side JavaScript function is called to handle the form with client-side JavaScript:</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">function</span> <span class="token function">setupLoginPage</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    
    <span class="token keyword">const</span> username <span class="token operator">=</span> document<span class="token punctuation">.</span><span class="token function">getElementById</span><span class="token punctuation">(</span><span class="token string">'username'</span><span class="token punctuation">)</span><span class="token punctuation">.</span>value
    <span class="token keyword">const</span> password <span class="token operator">=</span> document<span class="token punctuation">.</span><span class="token function">getElementById</span><span class="token punctuation">(</span><span class="token string">'password'</span><span class="token punctuation">)</span><span class="token punctuation">.</span>value
    
    <span class="token keyword">const</span> loginForm <span class="token operator">=</span> document<span class="token punctuation">.</span><span class="token function">getElementById</span><span class="token punctuation">(</span><span class="token string">'login'</span><span class="token punctuation">)</span>
    
    loginForm<span class="token punctuation">.</span><span class="token function">addEventListener</span><span class="token punctuation">(</span><span class="token string">'submit'</span><span class="token punctuation">,</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter">event</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
        
        <span class="token comment">// Let's assume the sendLoginRequest() function exists,</span>
        <span class="token comment">// and that it properly handle the login request</span>
        <span class="token comment">// (displays errors if something goes wrong, shows</span>
        <span class="token comment">// another page if the login succeeds, etc.).</span>
        <span class="token function">sendLoginRequest</span><span class="token punctuation">(</span>username<span class="token punctuation">,</span> password<span class="token punctuation">)</span>
        
    <span class="token punctuation">}</span><span class="token punctuation">)</span>
    
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br></div></div><p>The code above does not work as it should because it contains two different type of errors. Which are the two type of errors, and what changes do you need to do to the code to fix the errors?</p>
</template>
<template v-slot:sample-answer>
<p><strong>First type of error</strong> <br>
The text in the <code>&lt;input&gt;</code> elements are read immediately when <code>setupLoginPage()</code> is called. At this point in time, they will always be empty, so when the <code>&lt;form&gt;</code> is submitted, the user will always try to login with an empty username and an empty password, even if the user has entered something in the <code>&lt;input&gt;</code> elements.</p>
<p>To fix this type of error, move the following two lines of code:</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">const</span> username <span class="token operator">=</span> document<span class="token punctuation">.</span><span class="token function">getElementById</span><span class="token punctuation">(</span><span class="token string">'username'</span><span class="token punctuation">)</span><span class="token punctuation">.</span>value
<span class="token keyword">const</span> password <span class="token operator">=</span> document<span class="token punctuation">.</span><span class="token function">getElementById</span><span class="token punctuation">(</span><span class="token string">'password'</span><span class="token punctuation">)</span><span class="token punctuation">.</span>value
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><p>To inside the submit listener, so they are executed when the <code>&lt;form&gt;</code> is submitted.</p>
<p>(optimally, <code>document.getElementById('username|password')</code> can be in <code>setupLoginPage()</code>, but reading their <code>value</code> property should happen in the event listener)</p>
<p><strong>Second type of error</strong> <br>
Currently, when the <code>&lt;form&gt;</code> is submitted, the web browser will send its own standard HTTP request to the server, and then load the new page it gets back. Since this is an SPA, we don't want that to happen, but we should handle the form ourselves.</p>
<p>To prevent the web browser from sending its own standard HTTP request when the <code>&lt;form&gt;</code> is submitted, we should call <code>event.preventDefault()</code> in the submit listener.</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>0.25 points for text in <code>&lt;input&gt;</code> read too early</li>
<li>0.25 points for moving the read of <code>&lt;input&gt;</code> to the event listener</li>
<li>0.25 points for web browser loading new page when submitting the <code>&lt;form&gt;</code></li>
<li>0.25 points for calling <code>event.preventDefault()</code></li>
</ul>
</template>
</ExamQuestion>
<h3 id="question-4-1p" tabindex="-1"><a class="header-anchor" href="#question-4-1p" aria-hidden="true">#</a> Question 4 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>Alice has a webpage with HTML code that contains a frequently asked questions list, part of the code shown below (<code>&lt;dl&gt;</code> = <em>definition list</em>, <code>&lt;dt&gt;</code> = <em>definition term</em>, <code>&lt;dd&gt;</code> = <em>definition description</em>).</p>
<div class="language-html ext-html line-numbers-mode"><pre v-pre class="language-html"><code><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>h1</span><span class="token punctuation">></span></span>Frequently Asked Questions<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>h1</span><span class="token punctuation">></span></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>dl</span> <span class="token attr-name">id</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>faq<span class="token punctuation">"</span></span><span class="token punctuation">></span></span>
    
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>dt</span><span class="token punctuation">></span></span>What's your name?<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>dt</span><span class="token punctuation">></span></span>
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>dd</span><span class="token punctuation">></span></span>Alice<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>dd</span><span class="token punctuation">></span></span>
    
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>dt</span><span class="token punctuation">></span></span>How old are you?<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>dt</span><span class="token punctuation">></span></span>
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>dd</span><span class="token punctuation">></span></span>20<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>dd</span><span class="token punctuation">></span></span>
    
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>dt</span><span class="token punctuation">></span></span>Where do you live?<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>dt</span><span class="token punctuation">></span></span>
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>dd</span><span class="token punctuation">></span></span>Home<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>dd</span><span class="token punctuation">></span></span>
    
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>dl</span><span class="token punctuation">></span></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br></div></div><p>Alice don't want the answers to the questions to be shown until the user clicks on the corresponding question. To make that work, she first adds the following two CSS rules:</p>
<div class="language-css ext-css line-numbers-mode"><pre v-pre class="language-css"><code><span class="token comment">/* Hide all answers by default. */</span>
<span class="token selector">#faq dd</span><span class="token punctuation">{</span>
    display none<span class="token punctuation">;</span>
<span class="token punctuation">}</span>

<span class="token comment">/* Show answers with the class "show". */</span>
<span class="token selector">#faq dd.show</span><span class="token punctuation">{</span>
    <span class="token property">display</span><span class="token punctuation">:</span> initial<span class="token punctuation">;</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br></div></div><p>And then creates the following client-side JavaScript function (which will be called later):</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">function</span> <span class="token function">initializeFaqList</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    
    <span class="token keyword">const</span> dtElements <span class="token operator">=</span> document<span class="token punctuation">.</span><span class="token function">querySelectorAll</span><span class="token punctuation">(</span><span class="token string">'#faq dt'</span><span class="token punctuation">)</span>
    
    <span class="token keyword">for</span><span class="token punctuation">(</span><span class="token keyword">const</span> dt <span class="token keyword">of</span> dtElements<span class="token punctuation">)</span><span class="token punctuation">{</span>
        dt<span class="token punctuation">.</span><span class="token function">addEventListener</span><span class="token punctuation">(</span><span class="token string">'click'</span><span class="token punctuation">,</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter">event</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
            dt<span class="token punctuation">.</span>nextElementSibling<span class="token punctuation">.</span>classList<span class="token punctuation">.</span><span class="token function">toggle</span><span class="token punctuation">(</span><span class="token string">'show'</span><span class="token punctuation">)</span>
        <span class="token punctuation">}</span><span class="token punctuation">)</span>
    <span class="token punctuation">}</span>
    
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br></div></div><p>Has she implemented the functionality in a bad or good way? Justify your answer.</p>
</template>
<template v-slot:sample-answer>
<p>She has implemented the functionality in a bad way, because users with client-side JavaScript disabled in their web browsers won't see any answers, not even when they click on the questions. Client-side JavaScript should only be used to improve the user experience for users that have client-side JavaScript enabled, not to break functionality for users that have client-side JavaScript disabled.</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>1 point for correct answer and valid justification</li>
<li>-0.25 points for each weird claim</li>
</ul>
</template>
</ExamQuestion>
<h3 id="question-5-1p" tabindex="-1"><a class="header-anchor" href="#question-5-1p" aria-hidden="true">#</a> Question 5 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>When writing client-side JavaScript, do we need to worry about security vulnerabilities? Justify your answer.</p>
</template>
<template v-slot:sample-answer>
<p>Yes. For example, if the client-side JavaScript fetches a data string from the server that has been entered by another user (for example a username), and we want to display it to the user, we can't use this code:</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">const</span> fetchedUsername <span class="token operator">=</span> <span class="token string">"Alice"</span> <span class="token comment">// We have fetched this from the server somehow.</span>

document<span class="token punctuation">.</span><span class="token function">getElementById</span><span class="token punctuation">(</span><span class="token string">'aSpan'</span><span class="token punctuation">)</span><span class="token punctuation">.</span>innerHTML <span class="token operator">=</span> fetchedUsername
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><p>Because then we have introduced a XSS vulnerability when we used <code>innerHTML</code> (the fetched username can contain <code>&lt;script&gt;SOME JS CODE&lt;/script&gt;</code> a hacker entered when creating an account with that username, which the web browser would interpret as code and execute on other users' computers, and that code can tell the user's web browser to send bad HTTP requests to other websites).</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>1 point for correct answer and valid justification
<ul>
<li>Just saying something like <em>Yes, bad client-side JavaScript can be dangerous</em> or <em>Yes, security is something you should always worry about</em> is not enough (justify with an example showing where you have to think about it, or similar)</li>
</ul>
</li>
<li>0.25 points for giving a vague description of how a user can hack herself with bad client-side JavaScript, OR:</li>
<li>0.5 points for an example that clearly explains (with details) how using <code>innerHTML</code> can be a security vulnerability, but fails to explain how it can be used by a hacker to inject bad client-side JS in <strong>another</strong> user's web browser</li>
</ul>
</template>
</ExamQuestion>
<h2 id="containers" tabindex="-1"><a class="header-anchor" href="#containers" aria-hidden="true">#</a> Containers</h2>
<h3 id="question-6-1p" tabindex="-1"><a class="header-anchor" href="#question-6-1p" aria-hidden="true">#</a> Question 6 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>In Docker, explain the difference between the commands <code>RUN</code> and <code>CMD</code>.</p>
</template>
<template v-slot:sample-answer>
<p>The <code>RUN</code> command is used to specify a command that will be executed when building a new Docker image.</p>
<p>The <code>CMD</code> command is used to specify the command that should be executed to start the application in the Docker image, and it will be executed when the image starts running in a container.</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>0.25 points for each correct description</li>
<li>0.25 points for each description belonging to the right command</li>
<li>-0.1 points for saying something specific which is not necessarily true, like mentioning &quot;web application&quot; (Docker is not only for web applications)</li>
</ul>
<div class="custom-container warning"><p class="custom-container-title">Note!</p>
<p>Som students answer that the <code>RUN</code> command is used to start running an image in a new container. That's what the <code>docker run</code> command is used for.</p>
</div>
</template>
</ExamQuestion>
<h2 id="scaling" tabindex="-1"><a class="header-anchor" href="#scaling" aria-hidden="true">#</a> Scaling</h2>
<h3 id="question-7-2p" tabindex="-1"><a class="header-anchor" href="#question-7-2p" aria-hidden="true">#</a> Question 7 (2p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>In horizontal scaling, describe one benefit and one drawback with using sticky sessions compared to storing sessions in an external Redis database.</p>
</template>
<template v-slot:sample-answer>
<p>When using sticky sessions, the first time (of a session) a client sends an HTTP request to the server, the server (i.e. the load balancer) will remember which application server that handled the request, and all future request from that client (within the same session) will be forwarded to that same application server.</p>
<p>A benefit here, compared to storing the sessions in an external Redis database, is that the web application can store the sessions locally (on the application server itself), and therefor it will be much faster at carrying out the HTTP requests (it's faster to retrieve and store the sessions).</p>
<p>A drawback with sticky sessions, compared to storing the sessions in an external Redis database, is that it's a bit harder to scale down. As long as an application server has an active user session, we can't take down that application server, because that particular user is dependent on her requests is being forwarded to this specific application server, because that's where her session is stored. When storing the sessions in an external Redis database, all application servers are stateless, and it doesn't matter which application server that receives which client requests, making it easier to take down application servers when the load gets lower.</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>1 point for a benefit with using sticky sessions (faster to obtain and store sessions)</li>
<li>1 point for a drawback with using sticky sessions (harder to scale down, or harder to distribute load evenly (one application server can suddenly become very slow))</li>
<li>0.25 or 0.5 points for describing a small benefit/drawback, or mentioning something relevant to the big benefit/drawback, but not going all the way</li>
</ul>
<div class="custom-container warning"><p class="custom-container-title">Note!</p>
<p>Some students write a problem with sticky sessions is that a client's requests might be sent to different servers. That's not the case; <em>sticky sessions</em> means that all requests from one and the same client are forwarded to one and the same application server, so each application server can store the sessions locally.</p>
</div>
</template>
</ExamQuestion>
<h2 id="server-side" tabindex="-1"><a class="header-anchor" href="#server-side" aria-hidden="true">#</a> Server-side</h2>
<h3 id="question-8-2p" tabindex="-1"><a class="header-anchor" href="#question-8-2p" aria-hidden="true">#</a> Question 8 (2p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>Explain what MVC is, which parts it consists of and what each part is used for. Then also explain how web applications use MVC.</p>
<p>Don't write any code.</p>
</template>
<template v-slot:sample-answer>
<p>MVC (Model View Controller) is a design pattern describing a way to design/structure the code for the graphical user interface (GUI) part of an application.</p>
<p>The MVC design pattern consists of three different parts: models, views and controllers.</p>
<ul>
<li>A Model contains the data that should be visualized in the GUI</li>
<li>A View contains the code that visualizes the data in a Model</li>
<li>A Controller picks which Model and View that should be used, and also listen for user interaction in the View and do what the user wants when the interactions happens, such as changing the data in the Model or using another View</li>
</ul>
<p>When a web application makes use of MVC, the Controllers listen for incoming HTTP requests, and when they receive one, they create a Model with the data that should be sent back, and then passes that Model to a View, which embeds the data in HTML code, and then the Controller sends back the HTML code from the View in an HTTP response.</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>0.5 points for what MVC is:
<ul>
<li>0.25 points for design pattern</li>
<li>0.25 points for implementing GUI</li>
</ul>
</li>
<li>0.33 points for each part name and description of each part</li>
<li>0.5 points for how web applications use of MVC
<ul>
<li>0.25 points for controllers receive requests and send back responses</li>
<li>0.25 points for Views produce HTML</li>
</ul>
</li>
</ul>
<div class="custom-container warning"><p class="custom-container-title">Note!</p>
<p>Answering <em>Explain what MVC is</em> by just writing the acronym <em>Model View Controller</em> is not enough.</p>
<p>This question has nothing to do with a three-layered architecture, so no need (rather confusing) to involve that in your answer in this question.</p>
</div>
</template>
</ExamQuestion>
<h3 id="question-9-3p" tabindex="-1"><a class="header-anchor" href="#question-9-3p" aria-hidden="true">#</a> Question 9 (3p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>Explain what a three-layered architecture is, which parts it consists of and what each part is used for. Then also explain how web applications use a three-layered architecture.</p>
<p>Don't write any code.</p>
</template>
<template v-slot:sample-answer>
<p>A three-layered architecture is a way organize the code of an application in three different layers, each with their own responsibilities:</p>
<ul>
<li>The <em>Data Access Layer</em> contains the code for storing and retrieving the application's data. It usually provides CRUD (Create, Read, Update and Delete) operations for this</li>
<li>The <em>Business Logic Layer</em> contains the code for carrying out the tasks the user wants to do (the application's functionality), such as computing how much the user should pay for the products put in a shopping bag, verify payments, etc. It typically contains authorization (for example, only allowing the user to use the application if the user has a paid membership or if the test period is not over yet) and validation (for example, checking if data the user sends to the application is in correct format). Is uses the Data Access Layer to get access to the data</li>
<li>The <em>Presentation Layer</em> is responsible for implementing an interface through which the application can be used, for example a graphical user interface that humans can use. When the user interacts with the application through the interface (for example clicks on a button), the Presentation Layer asks the Business Logic Layer to execute the task the button is associated with</li>
</ul>
<p>In web applications, the Presentation Layer is responsible for receiving HTTP requests, and then ask the Business Logic Layer to carry out the requests. The Data Access Layer often store the data in a database. The Presentation Layer is also responsible for generating the HTTP response to send back, containing the HTML and CSS code the web browser use to display the webpage in the response.</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>0.5 points for what it is</li>
<li>0.25 points for what each part's name is</li>
<li>0.25 points for what each part is used for</li>
<li>0.5 points for how web app use it (HTTP, HTML, CSS in Presentation Layer, and description of the other layers)</li>
<li>0.5 points for good quality answers (many details, not using different names to describe the same thing, use the correct name on things, write proper and easy to read sentences, clearly explain which layer making use of which other layer, use singular and plural correctly, etc.)</li>
</ul>
<div class="custom-container warning"><p class="custom-container-title">Note!</p>
<p>Answering <em>Explain what a three-layered architecture is</em> with something like <em>It's an architecture</em>, or <em>It's an architecture consisting of three layers</em> is of course not a good way to do it. Anyone gets that understanding by just reading the name. You have to explain what it's an architecture of, and what that means (what is a layer? what does a layer consist of? Etc.).</p>
<p>The Presentation Layer doesn't have to make use of HTML and HTTP. This architecture can not only be used in web applications, but other type of applications too.</p>
<p>The Data Access Layer does not have to store data in a database. It's not called <em>Database Access Layer</em>.</p>
<p>Some students write the layers are independent of each other/know nothing about each others. That is not true. If it would be true, then would, for example, the Presentation Layer have no way of using the Business Logic Layer.</p>
<p>Many students write that the Presentation Layer receives HTTP requests, and then sends the requests to the Business Logic Layer. It really sounds like you're saying <em>the Business Logic Layer obtains the HTTP request</em>, and that is wrong; the Business Logic Layer shouldn't know the Presentation Layer makes use of HTTP at all, and therefor it can't receive HTTP requests from it. Saying that the Business Logic Layers <em>carries out</em> the request, or similar, is OK, because that does not imply the Business Logic Layers receives the HTTP request.</p>
</div>
</template>
</ExamQuestion>
<h3 id="question-10-1p" tabindex="-1"><a class="header-anchor" href="#question-10-1p" aria-hidden="true">#</a> Question 10 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>In a three-layered architecture, do we need to worry about SQL injections in the PL? Justify your answer.</p>
</template>
<template v-slot:sample-answer>
<p>No, the Presentation Layer is not aware of how the Data Access Layer stores the data, so it's not aware of the data is stored in a relational database at all (if that's the case), and therefor should not have any code concerning SQL (such as protection against SQL injections).</p>
<p>If the Presentation Layer would contain code protecting the application against SQL injections attack, then if we changed from a relational database to a NoSQL database in the future, we would not only need to make changes to the code in the Data Access Layer (which should be the case), but also change the code in the Presentation Layer (removing the SQL protection code), which should not be the case.</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>1 point for correct answer and valid justification</li>
<li>0.5 points for wrong answer but valid justification for the correct answer (?! 😕)</li>
</ul>
</template>
</ExamQuestion>
<h3 id="question-11-1p" tabindex="-1"><a class="header-anchor" href="#question-11-1p" aria-hidden="true">#</a> Question 11 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>In a three layered architecture, one layer exposes a function called <code>selectAllFromTheAccountsTable()</code>. Is that a good or bad name on the function? Justify your answer.</p>
</template>
<template v-slot:sample-answer>
<p>That's a bad name, because it exposes implementation details. The name suggest we can use the function to fetch data from a relational database. This might be true now, but if we in the future change the Data Access Layer to store the data in another way (in a JS array, or in a JSON file, or in a NoSQL database, etc.), the name would simply no longer make any sense, and we would need to rename the function to something like <code>getAllAccounts()</code>, and we would need to change the code in the Business Logic Layer making use of this function. We should not need to change the code in the Business Logic Layer like that, so it should have the more general name <code>getAllAccounts()</code> from the beginning.</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>1 point for correct answer and valid justification
<ul>
<li>-0.25 points for having an almost valid justification, but missing some important detail</li>
</ul>
</li>
</ul>
<div class="custom-container warning"><p class="custom-container-title">Note!</p>
<p>Some students claim the name of the function is good because it describes precisely what the function do. And yes, we want to have good descriptive names that reflects what data variables stores and what functions do, but usually (and crucially in interfaces), we don't want to expose implementation details. Since this function is used in another layer, its name should not expose implementation details.</p>
</div>
</template>
</ExamQuestion>
<h3 id="question-12-2p" tabindex="-1"><a class="header-anchor" href="#question-12-2p" aria-hidden="true">#</a> Question 12 (2p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>Explain what a foreign key constraint is, and show an example of how things can go wrong if they aren't used properly.</p>
</template>
<template v-slot:sample-answer>
<p>A foreign key constraint is a constraint in a relational database that enforces a relation between a foreign key field in posts in one table to the primary key field in posts in another table. By enforcing the constraint, the database won't allow posts to be inserted in the first table unless it has a relation to an existing post in the other table. They are really important when we need to enforce such relations, which is the case for most applications using relational databases.</p>
<p><strong>Example</strong> <br>
Imagine we have the two tables <code>Humans</code> and <code>Pets</code>. In our application, a pet must always belong to a human, because we don't want to have any homeless pets. So the field <code>humanId</code> in the <code>Pets</code> table has a foreign key constraint to the field <code>id</code> in the <code>Humans</code> table.</p>
<p>Thanks to this constraint, we can simply have a query like this to insert a new pet:</p>
<div class="language-sql ext-sql line-numbers-mode"><pre v-pre class="language-sql"><code><span class="token keyword">INSERT</span> <span class="token keyword">INTO</span> Pets <span class="token punctuation">(</span>humanId<span class="token punctuation">,</span> name<span class="token punctuation">)</span> <span class="token keyword">VALUES</span> <span class="token punctuation">(</span><span class="token number">23</span><span class="token punctuation">,</span> <span class="token string">"Fishy"</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>If no human with the id <code>23</code> exists, the foreign key constraint would be violated, and the database would complain and not create the pet.</p>
<p>If we wouldn't have a foreign key constraint, we would first need to send a query like this one to see if we have a human with the given id:</p>
<div class="language-sql ext-sql line-numbers-mode"><pre v-pre class="language-sql"><code><span class="token keyword">SELECT</span> <span class="token operator">*</span> <span class="token keyword">FROM</span> Humans <span class="token keyword">WHERE</span> id <span class="token operator">=</span> <span class="token number">23</span> <span class="token keyword">LIMIT</span> <span class="token number">1</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>And if we do get back a human, we can go ahead with creating the pet using the first query. However, before the database receives our &quot;create pet&quot; query, it might be that someone has deleted the human with id <code>23</code>, so we can't be 100% protected from having homeless pets this way, which is why foreign key constraints are better to use.</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>1 point for general explanation
<ul>
<li>-0.5 points for not knowing the difference between foreign key and foreign key constraint</li>
</ul>
</li>
<li>1 point for example of problem when a foreign key is not used</li>
</ul>
<div class="custom-container warning"><p class="custom-container-title">Note!</p>
<p>Many students don't know the difference between a <em>foreign key</em> and a <em>foreign key constraint</em>. A foreign key is a field in one table, whose value will refer to a value in the primary key field in another table (all together known as a <em>relationship</em>). You can have this relationship without having a foreign key constraint. A foreign key constraint can be added to enforce and configure the relationship.</p>
</div>
</template>
</ExamQuestion>
<h2 id="rest-api" tabindex="-1"><a class="header-anchor" href="#rest-api" aria-hidden="true">#</a> REST API</h2>
<h3 id="question-13-1p" tabindex="-1"><a class="header-anchor" href="#question-13-1p" aria-hidden="true">#</a> Question 13 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>Match each HTTP status code with its reason phrase.</p>
<p>Status codes: <code>200</code> <code>201</code> <code>400</code> <code>401</code> <code>404</code></p>
<p>Reason Phrases: <code>Not Found</code> <code>OK</code> <code>Created</code> <code>Bad Request</code> <code>Unauthorized</code></p>
<p>All needs to be matched correct to get any points.</p>
</template>
<template v-slot:sample-answer>
<ul>
<li><code>200</code>: <code>OK</code></li>
<li><code>201</code>: <code>Created</code></li>
<li><code>400</code>: <code>Bad Request</code></li>
<li><code>401</code>: <code>Unauthorized</code></li>
<li><code>404</code>: <code>Not Found</code></li>
</ul>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>2 points for</li>
</ul>
</template>
</ExamQuestion>
<h3 id="question-14-1p" tabindex="-1"><a class="header-anchor" href="#question-14-1p" aria-hidden="true">#</a> Question 14 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>In an HTTP response with the status code 201, explain what the value of the <code>Location</code> header represents. Also, give an example of a value it can contain, and explain how that value should be interpreted.</p>
</template>
<template v-slot:sample-answer>
<p>The <code>Location</code> header's value represents the URI of the newly created resource (sending a GET request for it would give you back the newly created resource). The value could for example be <code>/movies/123</code>, meaning that a new movie resource was created, and that the new movie resource got <code>123</code> as a unique identifier among all movie resources.</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>0.5 points for general explanation of the value of the <code>Location</code> header (saying it redirects the user there is wrong, because the status code is 201)</li>
<li>0.25 points for example of value</li>
<li>0.25 points for explanation of that example value</li>
</ul>
</template>
</ExamQuestion>
<h3 id="question-15-1p" tabindex="-1"><a class="header-anchor" href="#question-15-1p" aria-hidden="true">#</a> Question 15 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>Does it makes sense to put a user's password in an ID Token? Justify your answer.</p>
</template>
<template v-slot:sample-answer>
<p>No. The whole idea with ID Tokens is that they contain information about the logged in user without exposing the user's credentials (such as password). And passwords should never be stored in plain text anyway, so putting it in an ID Token would simply be a security vulnerability.</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>1 point for correct answer and valid justification</li>
<li>-0.25 points for answers writing strange things</li>
<li>0 points for answers with multiple justifications, and at least one of them is wrong</li>
</ul>
</template>
</ExamQuestion>
<h3 id="question-16-1p" tabindex="-1"><a class="header-anchor" href="#question-16-1p" aria-hidden="true">#</a> Question 16 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>Does it makes sense to put a user's password in an Access Token? Justify your answer.</p>
</template>
<template v-slot:sample-answer>
<p>No. A password is <em>authentication information</em>, and that can be used to prove who the user is. Access Tokens should only contain <em>authorization information</em>, and that should describe what a holder of the token is allowed to do.</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>1 point for correct answer and valid justification</li>
<li>-0.25 points for answers writing strange things</li>
<li>0 points for answers with multiple justifications, and at least one of them is wrong</li>
</ul>
<div class="custom-container warning"><p class="custom-container-title">Note!</p>
<p>Many students claim that it's bad to put sensitive information (such as passwords) in an Access Token since it's stored on the client and can end up in the hands of a hacker, which then would be able to read the user's password after decoding the token.</p>
<p>This is true WHEN the Access Token is self-contained, such as JWT tokens. Access Tokens don't have to be self-contained, so you really have to indicate in these type of answer that you are thinking of self-contained Access Tokens. I haven't punished that with point reduction this exam.</p>
</div>
</template>
</ExamQuestion>
<h2 id="security" tabindex="-1"><a class="header-anchor" href="#security" aria-hidden="true">#</a> Security</h2>
<h3 id="question-17-1p" tabindex="-1"><a class="header-anchor" href="#question-17-1p" aria-hidden="true">#</a> Question 17 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>Give an example of a client-side HTTP request the web browser will not send due to the Same-Origin Policy.</p>
</template>
<template v-slot:sample-answer>
<p>An HTTP <code>DELETE</code> request sent to a domain other than the one the client-side code making the request (such as JavaScript) comes from.</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>1 point for a valid example</li>
</ul>
<div class="custom-container warning"><p class="custom-container-title">Note!</p>
<p>The Same-Origin Policy do allow some GET and POST requests to be sent (mainly those that can be sent through the submission of a <code>&lt;form&gt;</code>), but they won't allow client-side JavaScript to read the responses unless CORS headers have been added to the response, telling the web browser it's OK for the client-side JavaScript code to read it.</p>
</div>
</template>
</ExamQuestion>
<h3 id="question-18-2p" tabindex="-1"><a class="header-anchor" href="#question-18-2p" aria-hidden="true">#</a> Question 18 (2p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>Web browsers started to support CORS around 2010. Today some companies still force their employees to run older versions of web browsers (from before 2010) that don't support CORS.</p>
<p>Given that all users don't have a web browser that supports CORS, can it be a security vulnerability to enable CORS on your web application? Justify your answer.</p>
</template>
<template v-slot:sample-answer>
<p>No, it can't, not based on that not all web browsers support CORS. CORS rather makes web browsers less secure by relaxing the Same-Origin Policy, and if a web browser doesn't support CORS, the Same-Origin Policy will simply not be relaxed, and remain as strict and secure as it already was.</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>2 points for correct answer and valid justification</li>
<li>-0.5 points for saying something irrelevant</li>
</ul>
</template>
</ExamQuestion>
<h2 id="code-writing" tabindex="-1"><a class="header-anchor" href="#code-writing" aria-hidden="true">#</a> Code Writing</h2>
<h3 id="question-19-1p" tabindex="-1"><a class="header-anchor" href="#question-19-1p" aria-hidden="true">#</a> Question 19 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>Implement the JavaScript function <code>isSumGreaterThan100(numbers)</code>, which receives an array with numbers, and returns:</p>
<ul>
<li><code>true</code> if the sum of the numbers is greater than 100</li>
<li><code>false</code> otherwise</li>
</ul>
<p>Sample usage:</p>
<ul>
<li><code>isSumGreaterThan100([50, 25, 10]) → false</code></li>
<li><code>isSumGreaterThan100([50, 100]) → true</code></li>
</ul>
</template>
<template v-slot:sample-answer>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">function</span> <span class="token function">isSumGreaterThan100</span><span class="token punctuation">(</span><span class="token parameter">numbers</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    
    <span class="token keyword">let</span> sum <span class="token operator">=</span> <span class="token number">0</span>
    
    <span class="token keyword">for</span><span class="token punctuation">(</span><span class="token keyword">const</span> number <span class="token keyword">of</span> numbers<span class="token punctuation">)</span><span class="token punctuation">{</span>
        sum <span class="token operator">+=</span> number
    <span class="token punctuation">}</span>
    
    <span class="token keyword">return</span> <span class="token number">100</span> <span class="token operator">&lt;</span> sum
    
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br></div></div></template>
<template v-slot:marking-guidelines>
<ul>
<li>1 point for a solution that largely works</li>
<li>-0.1 points for each bad name</li>
<li>-0.1 points for calling parameter <code>numbers</code>, but iteration variable <code>value</code> (use singular of parameter name!)</li>
<li>-0.1 points for using <code>let</code> when could use <code>const</code></li>
<li>-0.1 points for using <code>var</code> when could use <code>let</code></li>
<li>-0.1 points for storing the function in global variable without <code>const</code></li>
<li>-0.1 for using <code>anArray.length()</code> instead of <code>anArray.length</code></li>
<li>-0.1 points for not having semicolon after all statements (if that's the convention you use)</li>
<li>-0.1 points for having same code in <code>if</code> and <code>else</code> (factor it out)</li>
<li>-0.1 points for each line not properly indented</li>
<li>-0.1 points for naming <code>isSumGreaterThan100</code> wrong</li>
<li>-0.2 points for each global variable created/assigned in the function</li>
<li>-0.25 for implementing <code>!isSumGreaterThan100()</code></li>
<li>-0.25 for implementing <code>isSumGreaterThan_OR_EQUAL_TO_100()</code></li>
<li>-0.25 for iterating over indexes instead of values</li>
<li>-0.25 for using variable that doesn't exist/hasn't been initialized</li>
<li>-0.25 points for each syntax error</li>
</ul>
<div class="custom-container warning"><p class="custom-container-title">Note!</p>
<p>Some students think it's a good idea to put <code>100</code> in a constant named <code>oneHundred</code>. That is meaningless; reading <code>100</code> in the code gives the very same understanding as reading <code>oneHundred</code>, so stick to using <code>100</code> instead. When you have a better name on the value than just the amount it represents, then create a descriptive constant, like <code>const MAXIMUM_STUDENTS_IN_SCHOOL = 1500</code> instead of <code>const FIFTEEN_HUNDRED = 1500</code>.</p>
<p>The name of the function is <em>is<strong>Sum</strong>GreaterThan100</em>. As the name suggests, you obviously have to compute the <em><strong>sum</strong></em> of the numbers. What's a good name on the variable that stores the sum? <code>sum</code>! Many students instead call this variable something else, like <code>total</code>, <code>totalSum</code>, <code>summary</code>, <code>result</code>, etc., but this makes the code harder to read. Don't invent new names, stick to using existing names instead, and your code gets more consistent and easier to read.</p>
</div>
</template>
</ExamQuestion>
<h3 id="question-20-1p" tabindex="-1"><a class="header-anchor" href="#question-20-1p" aria-hidden="true">#</a> Question 20 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>Implement the client-side JavaScript function <code>makeCounter(button, startNumber)</code>, which receives an HTML Button object as the first argument, and makes that button function as a counter button (that is, <code>startNumber</code> should be used as the initial text in the button, and that text (number) should be incremented by 1 each time the button is clicked).</p>
<p>Sample usage:</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">const</span> aButton <span class="token operator">=</span> document<span class="token punctuation">.</span><span class="token function">getElementById</span><span class="token punctuation">(</span><span class="token string">'counter-button'</span><span class="token punctuation">)</span>
<span class="token function">makeCounter</span><span class="token punctuation">(</span>aButton<span class="token punctuation">,</span> <span class="token number">10</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><p>After the code above has been executed, the <code>&lt;button&gt;</code> with id <code>counter-button</code> should contain the text <code>10</code>, and clicking on it should change the text to <code>11</code>, and so one.</p>
<p>It should be possible to call the function multiple times with different buttons, and each counter button should function independently of the others.</p>
</template>
<template v-slot:sample-answer>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">function</span> <span class="token function">makeCounter</span><span class="token punctuation">(</span><span class="token parameter">button<span class="token punctuation">,</span> startNumber</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    
    <span class="token keyword">let</span> number <span class="token operator">=</span> startNumber
    button<span class="token punctuation">.</span>innerText <span class="token operator">=</span> number
    
    button<span class="token punctuation">.</span><span class="token function">addEventListener</span><span class="token punctuation">(</span><span class="token string">'click'</span><span class="token punctuation">,</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
        number <span class="token operator">+=</span><span class="token number">1</span>
        button<span class="token punctuation">.</span>innerText <span class="token operator">=</span> number
    <span class="token punctuation">}</span><span class="token punctuation">)</span>
    
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br></div></div></template>
<template v-slot:marking-guidelines>
<ul>
<li>1 point for a solution that largely works</li>
<li>-0.1 points for each bad name</li>
<li>-0.1 points for each misspelled name</li>
<li>-0.1 points for each parameter named wrong (names were given in the question)</li>
<li>-0.1 points for using <code>var</code> when could use <code>let</code></li>
<li>-0.1 points for not naming <code>click</code> event correct</li>
<li>-0.1 points for each line not properly indented</li>
<li>-0.1 points for not naming <code>innerText</code>/<code>innerHTML</code> correct</li>
<li>-0.1 points for storing the function in global variable without <code>const</code></li>
