        <span class="token comment">//   {id: 5, movieId: 5, rating: 3},</span>
        <span class="token comment">//   {id: 9, movieId: 5, rating: 9},</span>
        <span class="token comment">//   ...</span>
        <span class="token comment">// ]</span>
    <span class="token punctuation">}</span>
<span class="token punctuation">}</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br></div></div><p>Implement the function <code>getBestMovie(title1, title2, callback)</code>, which receives the title of two movies as arguments, and should send back the movie object with the highest average rating through the callback function.</p>
<p>Sample usage:</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token function">getBestMovie</span><span class="token punctuation">(</span><span class="token string">"Shrek"</span><span class="token punctuation">,</span> <span class="token string">"Die another day"</span><span class="token punctuation">,</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter">error<span class="token punctuation">,</span> bestMovie</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    <span class="token keyword">if</span><span class="token punctuation">(</span>error<span class="token punctuation">)</span><span class="token punctuation">{</span>
        console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span><span class="token string">"Problem fetching required info."</span><span class="token punctuation">,</span> error<span class="token punctuation">)</span>
    <span class="token punctuation">}</span><span class="token keyword">else</span><span class="token punctuation">{</span>
        console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span><span class="token string">"The best movie is "</span><span class="token operator">+</span>bestMovie<span class="token punctuation">.</span>title<span class="token operator">+</span><span class="token string">"."</span><span class="token punctuation">)</span>
    <span class="token punctuation">}</span>
<span class="token punctuation">}</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br></div></div><p>Important details:</p>
<ul>
<li>The callback function should be called only once</li>
<li>As soon as any function gives an error, the callback function should be called with that error (the other argument should be <code>null</code>)</li>
<li>The data should be fetched as fast as possible (loading times should be minimized)</li>
<li>When suitable, create helper functions (avoid repeating code)</li>
</ul>
</template>
<template v-slot:sample-answer>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">function</span> <span class="token function">getBestMovie</span><span class="token punctuation">(</span><span class="token parameter">title1<span class="token punctuation">,</span> title2<span class="token punctuation">,</span> callback</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    
    <span class="token comment">// This is the structure of the data we need to compute.</span>
    <span class="token keyword">const</span> movieAndAverageRatings <span class="token operator">=</span> <span class="token punctuation">[</span>
        <span class="token comment">// {movie: theMovieObject, averageRating: 0}</span>
        <span class="token comment">// {movie: theMovieObject, averageRating: 0}</span>
    <span class="token punctuation">]</span>
    
    <span class="token keyword">let</span> hasSentBackError <span class="token operator">=</span> <span class="token boolean">false</span>
    
    <span class="token comment">// Fetch the two movies in parallel.</span>
    <span class="token function">getMovieByTitle</span><span class="token punctuation">(</span>title1<span class="token punctuation">,</span> onMovieFetched<span class="token punctuation">)</span>
    <span class="token function">getMovieByTitle</span><span class="token punctuation">(</span>title2<span class="token punctuation">,</span> onMovieFetched<span class="token punctuation">)</span>
    
    <span class="token keyword">function</span> <span class="token function">onMovieFetched</span><span class="token punctuation">(</span><span class="token parameter">error<span class="token punctuation">,</span> movie</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
        
        <span class="token keyword">if</span><span class="token punctuation">(</span>hasSentBackError<span class="token punctuation">)</span><span class="token punctuation">{</span>
            
            <span class="token comment">// Something has already gone wrong, we don't need to continue.</span>
            
        <span class="token punctuation">}</span><span class="token keyword">else</span> <span class="token keyword">if</span><span class="token punctuation">(</span>error<span class="token punctuation">)</span><span class="token punctuation">{</span>
            
            <span class="token function">callback</span><span class="token punctuation">(</span>error<span class="token punctuation">,</span> <span class="token keyword">null</span><span class="token punctuation">)</span>
            hasSentBackError <span class="token operator">=</span> <span class="token boolean">true</span>
            
        <span class="token punctuation">}</span><span class="token keyword">else</span><span class="token punctuation">{</span>
            
            <span class="token comment">// Fetch the reviews for the movie.</span>
            <span class="token function">getReviewsByMovieId</span><span class="token punctuation">(</span>movie<span class="token punctuation">.</span>id<span class="token punctuation">,</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter">error<span class="token punctuation">,</span> reviews</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
                <span class="token function">onReviewsFetched</span><span class="token punctuation">(</span>error<span class="token punctuation">,</span> reviews<span class="token punctuation">,</span> movie<span class="token punctuation">)</span>
            <span class="token punctuation">}</span><span class="token punctuation">)</span>
            
        <span class="token punctuation">}</span>
        
    <span class="token punctuation">}</span>
    
    <span class="token keyword">function</span> <span class="token function">onReviewsFetched</span><span class="token punctuation">(</span><span class="token parameter">error<span class="token punctuation">,</span> reviews<span class="token punctuation">,</span> movie</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
        
        <span class="token keyword">if</span><span class="token punctuation">(</span>hasSentBackError<span class="token punctuation">)</span><span class="token punctuation">{</span>
            
            <span class="token comment">// Something has already gone wrong, we don't need to continue.</span>
            
        <span class="token punctuation">}</span><span class="token keyword">else</span> <span class="token keyword">if</span><span class="token punctuation">(</span>error<span class="token punctuation">)</span><span class="token punctuation">{</span>
            
            <span class="token function">callback</span><span class="token punctuation">(</span>error<span class="token punctuation">,</span> <span class="token keyword">null</span><span class="token punctuation">)</span>
            hasSentBackError <span class="token operator">=</span> <span class="token boolean">true</span>
            
        <span class="token punctuation">}</span><span class="token keyword">else</span><span class="token punctuation">{</span>
            
            <span class="token comment">// Compute average and remember it.</span>
            movieAndAverageRatings<span class="token punctuation">.</span><span class="token function">push</span><span class="token punctuation">(</span><span class="token punctuation">{</span>
                movie<span class="token punctuation">,</span>
                averageRating<span class="token operator">:</span> <span class="token function">getAverageRating</span><span class="token punctuation">(</span>reviews<span class="token punctuation">)</span>
            <span class="token punctuation">}</span><span class="token punctuation">)</span>
            
            <span class="token comment">// Check if we have computed average rating for both movies.</span>
            <span class="token keyword">if</span><span class="token punctuation">(</span>movieAndAverageRatings<span class="token punctuation">.</span>length <span class="token operator">==</span> <span class="token number">2</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
                
                <span class="token comment">// Check which movie has best rating.</span>
                <span class="token keyword">if</span><span class="token punctuation">(</span>movieAndAverageRatings<span class="token punctuation">[</span><span class="token number">0</span><span class="token punctuation">]</span><span class="token punctuation">.</span>averageRating <span class="token operator">&lt;</span> movieAndAverageRatings<span class="token punctuation">[</span><span class="token number">1</span><span class="token punctuation">]</span><span class="token punctuation">.</span>averageRating<span class="token punctuation">)</span><span class="token punctuation">{</span>
                    <span class="token function">callback</span><span class="token punctuation">(</span><span class="token keyword">null</span><span class="token punctuation">,</span> movieAndAverageRatings<span class="token punctuation">[</span><span class="token number">1</span><span class="token punctuation">]</span><span class="token punctuation">.</span>movie<span class="token punctuation">)</span>
                <span class="token punctuation">}</span><span class="token keyword">else</span><span class="token punctuation">{</span>
                    <span class="token function">callback</span><span class="token punctuation">(</span><span class="token keyword">null</span><span class="token punctuation">,</span> movieAndAverageRatings<span class="token punctuation">[</span><span class="token number">0</span><span class="token punctuation">]</span><span class="token punctuation">.</span>movie<span class="token punctuation">)</span>
                <span class="token punctuation">}</span>
                
            <span class="token punctuation">}</span>
            
        <span class="token punctuation">}</span>
        
    <span class="token punctuation">}</span>
    
    <span class="token keyword">function</span> <span class="token function">getAverageRating</span><span class="token punctuation">(</span><span class="token parameter">reviews</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
        
        <span class="token keyword">let</span> ratingsSum <span class="token operator">=</span> <span class="token number">0</span>
        
        <span class="token keyword">for</span><span class="token punctuation">(</span><span class="token keyword">const</span> review <span class="token keyword">of</span> reviews<span class="token punctuation">)</span><span class="token punctuation">{</span>
            ratingSum <span class="token operator">+=</span> review<span class="token punctuation">.</span>rating
        <span class="token punctuation">}</span>
        
        <span class="token keyword">return</span> ratingSum <span class="token operator">/</span> reviews<span class="token punctuation">.</span>length
        
    <span class="token punctuation">}</span>
    
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br><span class="line-number">21</span><br><span class="line-number">22</span><br><span class="line-number">23</span><br><span class="line-number">24</span><br><span class="line-number">25</span><br><span class="line-number">26</span><br><span class="line-number">27</span><br><span class="line-number">28</span><br><span class="line-number">29</span><br><span class="line-number">30</span><br><span class="line-number">31</span><br><span class="line-number">32</span><br><span class="line-number">33</span><br><span class="line-number">34</span><br><span class="line-number">35</span><br><span class="line-number">36</span><br><span class="line-number">37</span><br><span class="line-number">38</span><br><span class="line-number">39</span><br><span class="line-number">40</span><br><span class="line-number">41</span><br><span class="line-number">42</span><br><span class="line-number">43</span><br><span class="line-number">44</span><br><span class="line-number">45</span><br><span class="line-number">46</span><br><span class="line-number">47</span><br><span class="line-number">48</span><br><span class="line-number">49</span><br><span class="line-number">50</span><br><span class="line-nu